body {
  font-family: 'Slabo 27px', serif;
  overflow: hidden;
}

.wrapper {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 50px calc(100vh - 50px);
  overflow: auto;
  position: relative;
}

header,
.main {
  grid-column: 1;
}

header ul {
  background-color: #7c7fbc;
  list-style: none;
  margin: 0;
  padding: 0;
}

header li {
  display: inline-block;
}

header a {
  color: #fff;
  display: block;
  line-height: 50px;
  padding-left: 15px;
  padding-right: 15px;
  text-decoration: none;
}

header a:hover {
  background-color: #444;
  color: #fff;
}

.main {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 0 10px;
}

a {
  color: #7c7fbc;
}

.loading {
  font-size: 1rem;
  line-height: calc(100vh - 50px);
  text-align: center;
}

.blog-entry {
  margin: 3rem 0 2rem;
}

.blog-entry h1 {
  margin-top: 0;
}

blockquote {
  border-left: 4px solid #eee;
  margin: 0;
  padding: 3px 0 3px 15px;
}

/* only for inline code */
code:not(.hljs) {
  background-color: #eee;
  border-radius: 4px;
  padding: 1px 4px;
}

.article-list {
  margin: 3rem 0 2rem;
  padding: 0 10px;
}

.article-list a:visited {
  color: #72bd21;
}

.article-list h2 {
  border-bottom: 2px solid #eee;
  font-size: 1.25rem;
  font-style: italic;
  text-transform: uppercase;
  padding: 0;
  margin-bottom: 0.8rem;
  margin-top: 0;
}

.article-list article {
  padding-top: 18px;
  position: relative;
}

.article-list article + article {
  margin-top: 18px;
}

.article-list article h3 {
  margin: 0;
}

.article-list article p {
  font-size: 0.9rem;
  margin: 6px 0 0;
}

.article-list .date {
  color: #444;
  display: block;
  font-size: 0.8rem;
  font-weight: 900;
  position: absolute;
  top: 0;
}

.article-list.imsa .field-name-title-field,
.article-list.imsa .field-name-field-news-photo-gallery,
.article-list.imsa .field-name-field-date,
.article-list.imsa .field-name-body .field-label {
  display: none;
}

@media (min-width: 800px) {
  .wrapper {
    grid-template-columns: 15px auto 15px;
    grid-template-rows: 50px calc(100vh - 50px);
  }

  header {
    grid-column: 1 / span 3;
  }

  header ul {
    display: grid;
    grid-template-columns: 1fr auto auto auto;
  }

  header li:first-child a {
    display: inline-block;
  }

  .main {
    padding: 0;
    grid-column: 2 / span 1;
  }

  .home {
    display: grid;
    grid-template-columns: 50% 50%;
  }

  .blog-entry-content h2,
  .blog-entry-content p {
    max-width: 800px;
  }
}

@media (min-width: 1200px) {
  .wrapper {
    grid-template-columns: 15px auto 15px;
  }

  .home {
    display: grid;
    grid-template-columns: 33.3% 33.3% auto;
  }

  .blog-entry-content pre {
    max-width: 1024px;
  }
}