.header {
  position: relative;
}

.inputWrapper {
  margin-left: -0.2rem;
  margin-top: -0.4rem;
  opacity: 0;
  position: absolute;
  right: 0;
  text-align: left;
  transition: width 0.35s, opacity 0.35s;
  width: 60%;
  z-index: 2;
}


.inputWrapperIn {
  opacity: 1;
  width: 100%;
}

.input {
  border: 1px solid #eee;
  border-radius: 1rem;
  box-shadow: 0 0 1rem rgba(0,0,0,0.5);
  height: 2rem;
  font-family: 'Slabo 27px', serif;
  font-size: 1.25rem;
  line-height: 1.15rem;
  padding: 0 12px;
  width: calc(100% - 24px);
}

.input:focus {
  outline: none;
  box-shadow: 0 0 1rem blue;
}

.searchButton,
.cancelButton {
  background: none;
  border: none; 
  color: #7c7fbc;
  cursor:  pointer;
  font:  inherit;
  font-size: 1rem;
  font-style: normal;
  padding: 0 3px;
  position: absolute;
  right: 0;
  text-decoration: underline;
  top: 0;
  z-index: 1;
}

.cancelButton {
  line-height: 2rem;
  padding: 0 12px;
}